.container {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.page {
  position: absolute;
  width: 100%;
  height: 100%;
}

.page-visible {
  display: block;
}

.page-hidden {
  display: none;
}

.arrow-img {
  position: absolute;
  left: 50%;
  bottom: 4vw;
  margin-left: -5.6vw;
  width: 11.2vw;
  animation: arrow_img 1s linear infinite;
  z-index: 11;
}

@keyframes arrow_img {
  0%,
  to {
    transform: translateY(1.33333333vw);
  }

  50% {
    transform: translateY(0);
  }
}
