.layer {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(102, 102, 102, 0.92);
  z-index: 12;
  display: flex;
  align-items: center;
  justify-content: center;
  display: none;
  animation: fadeIn 0.5s ease;
}

.layer .box {
  display: flex;
  flex-direction: column;
}

.layer .close {
  width: 10.666667vw;
  margin: 0 auto;
  margin-top: 2vw;
}

