@keyframes listslideUp {
  from {
    transform: translateY(100%); /* 初始位置：向下移动自身高度的100% */
    opacity: 0; /* 完全透明 */
    height: 0; /* 初始高度为0 */
  }
  to {
    transform: translateY(0); /* 最终位置：回到原位 */
    opacity: 1; /* 完全不透明 */
    height: auto; /* 自动适应内容高度 */
  }
}
